* { 
	box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 0;
  padding: 0;
  color: white;
  text-align: center;
}

input {
  color: black;
}

ul {
  list-style-type: none;
}

.content {
  background: rgb(52, 52, 116);
  height: 93vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: scroll;
}

button {
  border: 0;
  border-radius: 2px;
  padding: 0.5rem;
  cursor: pointer;
}

.message {
  padding: 1rem;
  border-radius: 8px;
}

.error {
  background-color:lightcoral;
  color:brown;
}

.success {
  background-color: darkseagreen;
  color: green;
}

.conditional-content {
  height: 10vh;
}
#navbar {
    background-color: rgb(19, 10, 80);
    padding: 1rem;
    height: 7vh;
}

#navbar-list {
    display: flex;
    justify-content: space-evenly;
}

a {
    text-decoration: none;
}
#add-new {
    display: flex;
    flex-direction: column;
    align-items: center;
}

h4 {
    margin-bottom: 2rem;
}

.form-add-new {
    margin-bottom: 2rem;
}

#add-new-button {
    background-color: dodgerblue;
}
#quiz {
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

button {
    margin: 1.5rem;
}

#random-word {
    padding: 2rem;
}

#show-random-button {
    background-color: darkorchid;
}

#guess-button {
    background-color: green;
}

#delete-button {
    background-color: red;
}
#full-list {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

#fulllist-item {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
}

#delete-from-list-button {
    margin: 0 0 0 1rem;
    background-color: red;
    padding: 0.3rem;
}
